import { createStore } from "vuex";

const store = createStore({
  state() {
    return {
      isLogin: false,
      userName: "",
      userAvatar: "",
      userId: "",
    };
  },
  mutations: {
    setLoginState(state) {
      state.isLogin = true;
    },
    setUserName(state, name) {
      state.userName = name;
    },
    setUserAvatar(state, avatar) {
      state.userAvatar = avatar;
    },
    setUserId(state, id) {
      state.userId = id;
    },
  },
});

export default store;
